/* ==========================================================================
    Base
========================================================================== */

html {
    // height: 100%;
    font-size: 1em;
    line-height: 1em;

    // If Javascript is turned off...
    // &.no-js {}
}

body {
    position: relative;
    width: 100%;
    // height: 100%;
    min-height: 100vh;
    // overflow-x: hidden;
    // overflow-y: auto;
    font-family: $font-main;
    // font-size: clamp(100%, 1rem + 2vw, 1.33em);
    font-size: 1em;
    background-color: $white--darker;
    color: $black;

    // crisp typography rendering
    text-rendering: optimizeLegibility;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;

    &.no-scrollingPlz {
        overflow: hidden;
    }

    // "Overflow" scrolling on large monitors
    @include media(">=screen1600") {
        padding-bottom: 100px;
    }
    @include media(">=screen1920") {
        padding-bottom: 150px;
    }
}

/**
* === SITE ===
*/

.o-site {
    position: relative;
    max-width: 100%;
    min-height: 100vh;
    padding-top: 0;
    padding-bottom: 72px;
    transition: $speed linear;

    @include media(">=screen600") {
        padding-bottom: 0;

        &.-activeNavigationAreaUpTopButNotWhenScrolling {
            padding-top: 100px;
        }
    }

    @include media(">=screen1600") {
        max-width: 1440px;
        margin-left: auto;
        margin-right: auto;
        border-bottom-left-radius: $base-border-radius;
        border-bottom-right-radius: $base-border-radius;
        box-shadow: #{$gutter-sm} 0 #{$gutter * 0.67} -#{$gutter * 0.67} fade-out($black, 0.4),
                    -#{$gutter-sm} 0 #{$gutter * 0.67} -#{$gutter * 0.67} fade-out($black, 0.4);
    }

    @include media(">=screen1920") {
        max-width: 1600px;
    }
}

