/* purple: #673AB7 */
/* light: #EDEDED */
/* black: #000000 */

#cssda-badge {
	// /* monogram position here */
	// position: absolute;
	// top: calc(50% - 45px);
	// left: calc(50% - 45px);
	// /*  */
	width: 90px;
	height: 90px;
	border-radius: 90px;
	background: #EDEDED;
	transition: all .25s cubic-bezier(.615, .19, .305, .91);
	cursor: pointer;
}

#cssda-badge:hover {
	animation: bounce .75s linear both;
	background: #673AB7;
}

#cssda-badge svg {
	position: absolute;
	// top: 0;
	left: 0;
	width: 90px;
	height: 90px;
	fill: #000000;
}

#cssda-badge:hover svg {
	fill: #EDEDED;
}

.cssda-badge-content {
	position: absolute;
	width: 90px;
	height: 90px;
	transition: all .12s cubic-bezier(.615, .19, .305, .91);
}

#cssda-badge:hover .cssda-badge-content {
	transform: rotate(360deg);
}

#cssda-badge:hover #cssda-badge-logo {
	transform: scale(1.1);
}

@media only screen and (max-width: 700px) {
	#cssda-badge {
		// /* monogram responsive position here */
		// top: calc(50% - 30px);
		// left: calc(50% - 30px);
		// /*  */
		width: 60px;
		height: 60px;
	}

	#cssda-badge svg {
		width: 60px;
		height: 60px;
	}

	#cssda-badge-logo {
		transform: scale(1.35);
	}

	#cssda-badge:hover #cssda-badge-logo {
		transform: scale(1.5);
	}

	#cssda-badge-title {
		display: none;
	}

	#cssda-badge-award {
		display: none;
	}
}

